body,html{width:100%;height:100%;overflow:hidden}body{background:#141c25;color:#fff}*{margin:0;padding:0;box-sizing:border-box;outline:none;font-family:"Lato", Arial, sans-serif}a{text-decoration:none}h1{color:#141c25}h2{color:#8a9198}#root{height:100%}ul{list-style:none}#app{height:100%}.row{display:flex;height:calc(100% - 60px)}.container{width:100%}#button{min-width:160px;height:48px;border:none;margin:12px 12px 0 0;border-radius:0;text-transform:uppercase;font-weight:bold;cursor:pointer}#button:last-child{margin-right:0}#button.primary{background:#ea4c4c;color:#fff}#button.primary:hover{color:#fff;background:#e83e3e}#button.primary:active{background:#e62727}#button.outline{background:transparent;color:#fff;border:1px solid #fff}#button.outline:hover{color:#fff;background:rgba(255,255,255,0.05)}#button.outline:active{background:rgba(255,255,255,0.07)}#button svg{position:relative;top:1px;margin-right:.5em}

body,html{width:100%;height:100%;overflow:hidden}body{background:#141c25;color:#fff}*{margin:0;padding:0;box-sizing:border-box;outline:none;font-family:"Lato", Arial, sans-serif}a{text-decoration:none}h1{color:#141c25}h2{color:#8a9198}#root{height:100%}ul{list-style:none}#app{height:100%}.row{display:flex;height:calc(100% - 60px)}.container{width:100%}#side-menu{width:240px;min-width:240px;height:100%;transition:all ease 0.4s;background:#0d1218}#side-menu .menu{margin-top:44px}#side-menu .menu li{color:#8a9198;display:flex;align-items:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;padding:0 20px;font-size:14px;height:48px;margin-bottom:4px}#side-menu .menu li svg{min-width:1em;margin-right:1em}#side-menu .menu li span{transition:all linear 0.1s 0.3s}#side-menu .menu li.active{color:#ea4c4c;font-weight:700;background:#18212c}#side-menu .menu li.active:hover{color:#ea4c4c;background:#18212c}#side-menu .menu li:hover{color:#fff;background:#141c25}#side-menu .menu li:active{background:#161f28}#side-menu.compress{transition:all linear 0.2s;width:48px;min-width:48px}#side-menu.compress .menu li svg{margin:0}#side-menu.compress .menu li span{transition:none;opacity:0;position:absolute;margin:0}@media (max-width: 981px){#side-menu{position:fixed;bottom:0;left:0;width:100%;z-index:+5;height:52px}#side-menu .menu{display:flex;align-items:center;margin:0;height:100%}#side-menu .menu li{height:100%;margin:0;flex:1 1;flex-direction:column;justify-content:center;align-items:center;padding:0}#side-menu .menu li span{font-size:10px;display:none}#side-menu .menu li svg{margin:0 0 7px}#side-menu .menu li.active span{display:block}}

