@mixin flex {
  display: flex;
}
@mixin flex-center {
  display: flex;
  align-items: center;
}
@mixin flex-center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin flex-center-spaced {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
